.switch {
  margin: auto;
  position: relative;
}

.switch__icon, .switch__input {
  display: block;
}

.switch__icon {
  width: .75em;
  height: .75em;
  transition: opacity .35s cubic-bezier(.65, .05, .36, 1), transform .35s cubic-bezier(.65, .05, .36, 1);
  position: absolute;
  top: .375em;
  right: .375em;
}

.switch__icon polyline {
  transition: stroke-dashoffset .35s cubic-bezier(.65, .05, .36, 1);
}

.switch__icon--light, .switch__icon--light polyline {
  transition: all .35s cubic-bezier(.65, .05, .36, 1);
}

.switch__icon--dark {
  opacity: 0;
  transition: all .35s cubic-bezier(.65, .05, .36, 1);
  transform: translateX(-.75em)rotate(30deg)scale(.75);
}

.switch__input {
  width: 3em;
  height: 1.5em;
  -ms-appearance: none;
  appearance: none;
  -webkit-tap-highlight-color: transparent;
  cursor: pointer;
  background-color: #6eb3f7;
  border-radius: .75em;
  outline: rgba(0, 0, 0, 0);
  transition: background-color .35s cubic-bezier(.65, .05, .36, 1), box-shadow .15s linear;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.switch__input:focus-visible {
  box-shadow: 0 0 0 .125em hsl(var(--hue), 90%, 50%), .125em .125em .25em hsla(var(--hue), 90%, 10%, .2);
}

.switch__input:before, .switch__input:after {
  content: "";
  display: block;
  position: absolute;
}

.switch__input:before {
  border-radius: inherit;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  transition: background-color .35s cubic-bezier(.65, .05, .36, 1);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.switch__input:after {
  width: 1.25em;
  height: 1.25em;
  z-index: 1;
  background-color: #fff;
  border-radius: 50%;
  transition: background-color .35s cubic-bezier(.65, .05, .36, 1), transform .35s cubic-bezier(.65, .05, .36, 1);
  top: .125em;
  left: .125em;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.switch__input:checked {
  background-color: #a30ac2;
}

.switch__input:checked:before {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
}

.switch__input:checked:after {
  transform: translateX(1.5em);
}

.switch__input:checked ~ .switch__icon--light, .switch__input:checked ~ .switch__icon--light polyline {
  transition: all .35s cubic-bezier(.65, .05, .36, 1);
}

.switch__input:checked ~ .switch__icon--light {
  opacity: 0;
  transform: translateX(-.75em)rotate(-30deg)scale(.75);
}

.switch__input:checked ~ .switch__icon--light polyline {
  stroke-dashoffset: 1.5px;
}

.switch__input:checked ~ .switch__icon--dark {
  opacity: 1;
  transition: all .35s cubic-bezier(.65, .05, .36, 1);
  transform: translateX(-1.5em);
}

.switch__sr {
  width: 1px;
  height: 1px;
  position: absolute;
  overflow: hidden;
}

